import React, { useState, useEffect} from 'react';
import { connect }             from 'react-redux';
import { actChangeSearchRoom } from '../../redux/action/rooms';

function Form(props) {   
    const search = props.search;
    const [ min, setMin ]           = useState('');
    const [ max, setMax ]           = useState('');
    const [ sort, setSort ]         = useState('asc');
    const [ district, setDistrict ] = useState('novalue');
  
    useEffect(()=>{
        setMin(search.price_min);
        setMax(search.price_max);
        setSort(search.sort); 
        setDistrict(search.district)
    },[search])

    function handleSubmit(e){
        let search = {
            price_min: min,
            price_max: max,
            name: '',
            sort,
            district
        }
        e.preventDefault();
        props.onClickSubmit(search);        
        props.history.push("/rooms/1")
    }
   
    return (      
        <section className="ftco-booking">
            <div className="container">
                <div className="row">
                    <div className="col-lg-12">
                        <form className="booking-form" method = "POST" onSubmit = {handleSubmit} >
                            <div className="row">
                                <div className="col-md d-flex">
                                    <div className="form-group p-4 align-self-stretch d-flex align-items-end">
                                        <div className="wrap">
                                            <label>Price</label>
                                            <input type="text" className="form-control" value={min} onChange={e => setMin(e.target.value)} placeholder="Price Min" />
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md d-flex">
                                    <div className="form-group p-4 align-self-stretch d-flex align-items-end">
                                        <div className="wrap">
                                            <label >Price</label>
                                            <input type="text" className="form-control" value = {max} onChange = {e => setMax(e.target.value)} placeholder="Price Max" />
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-3 d-flex">
                                    <div className="form-group p-4 align-self-stretch d-flex align-items-end">
                                        <div className="wrap">
                                            <label >Sort</label>
                                            <div className="form-field">
                                                <div className="select-wrap">
                                                    <div className="icon"><span className="ion-ios-arrow-down" /></div>
                                                    <select name="true" className="form-control" onChange = { e => setSort(e.target.value)}>
                                                        <option value = "novlue">Choose Sort</option>
                                                        <option value = "asc">From Min to Max</option>
                                                        <option value = "desc">From Max to Min</option>                                                      
                                                    </select>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md d-flex">
                                    <div className="form-group p-4 align-self-stretch d-flex align-items-end">
                                        <div className="wrap">
                                            <label>Districts</label>
                                            <div className="form-field">
                                                <div className="select-wrap">
                                                    <div className="icon"><span className="ion-ios-arrow-down" /></div>
                                                    <select name="true" className="form-control" onChange = { e => setDistrict(e.target.value)}>
                                                        <option value = "novlue">Choose Districts</option>
                                                        <option value = "1">Districts 1</option>
                                                        <option value = "2">Districts 2</option>
                                                        <option value = "3">Districts 3</option>
                                                        <option value = "4">Districts 4</option>
                                                        <option value = "5">Districts 5</option>
                                                    </select>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md d-flex">                                    
                                    <div className="form-group d-flex align-self-stretch">                                     
                                        <input type="submit" value="Check Availability" className="btn btn-primary py-3 px-4 align-self-stretch" />
                                    </div>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </section>
    );
}


const mapDispatchtoProps = (dispatch, ownProps) => {
    return {      
        onClickSubmit: (search) => {
            dispatch(actChangeSearchRoom(search))
        }
    }
}

export default connect(null, mapDispatchtoProps)(Form);


