export const actfechBlogsRequest = (search, pagination) =>{    
    return (dispacth) =>{
        return CallApi('blogs/blogpage', 'GET', {search, pagination} ).then( (res) =>{          
            if(res !== undefined && res.status === 200 ){   
              dispacth(actfechBlogs(res.data))
            }
        })
    }
}
export const actfechBlogs = (blogs) =>{
    return { 
        type: types.FECTH_BLOGS,
        blogs
    }
}


export const actfechCountBlogsRequest = (search) =>{
    return (dispacth) =>{
        return CallApi('blogs/blogcount', 'GET', {search } ).then( (res) =>{            
            if(res !== undefined && res.status === 200 ){   
              dispacth(actfechCountBlogs(res.data))
            }
        })
    }
}
export const actfechCountBlogs = (countBlog) =>{
    return {
        type: types.FECTH_COUNT_BLOGS,
        countBlog
    }
}

export const actChangeCurrentPageBlog =(page)=>{    
    return {
        type: types.CHANGE_CURRENT_PAGE_BLOG,
        page
    }
}

   
export const actfechBlogDetailRequest = (slug) => {    
    return async (dispacth) => {
        return await CallApi('blogs/blogdetail', 'GET', {slug } ).then((res) => { //slug là 1 chuỗi                  
            if (res !== undefined && (res.status === 200 || res.status === 304)) {
                dispacth(actfechBlogDetail(res.data))
            }
        });
    };   
}

export const actfechBlogDetail = (blogdetail) => {
    return {
        type: types.FECTH_BLOG_DETAIL,
        blogdetail
    }
}

//change search blog
export const actChangeSearchBlog = (search) =>{    
    return { 
        type: types.SEARCH_BLOG,
        search
    }    
}
