import React, {useState, useEffect } from 'react';
import { connect }     from 'react-redux';
import PanelDetail     from '../Components/PanelDetail';
import RoomDetail      from '../Components/RoomPage/RoomDetail';
import Instagram       from '../Components/Instagram';
import { actFechBlogResentRequest } from '../redux/action/blogs';
import { actFecthImagePanelRequest }     from '../redux/action/carousels';
import { actfechRoomDetailRequest, actFecthRoomOrtherRequest}  from '../redux/action/rooms';


function RoomPageDetail(props) {   
    const slug   = props.match.params.slug;
    const history = props.history;
    const [room, setRoom ] = useState({});  
    const [blogs, setBlogs] = useState([]);

  
    useEffect(()=>{
        setRoom(props.roomDetail) 
    },[props.roomDetail])

    useEffect(()=>{
        setBlogs(props.blogs)
    },[props.blogs]) //eslint-disable-line
   
    useEffect(()=>{
        props.fechRoomDetailRequest(slug);
    },[slug]) //eslint-disable-line
     //lấy dữ liệu
     useEffect(() => {       
        props.fecthImagePanelRequest();
        props.fechBlogResentRequest();             
    },[]) //eslint-disable-line

    useEffect(()=>{
        props.fecthRoomOrtherRequest(slug);
    },[slug]) //eslint-disable-line

    useEffect(() => {
        const list = document.querySelectorAll('script[src = "js/main.js"]');
        if (list.length > 0) {
            document.body.removeChild(list[0])
        }
        const script = document.createElement('script');
        script.src = 'js/main.js';
        document.body.appendChild(script);
    },[props.blogs, slug])

    return (
        <div >
            <PanelDetail name = "room single" link = "/rooms/1" history = {history} />
            <RoomDetail room ={room} blogs = {blogs} roomsOrther = {props.roomsOrther} history = {history} />
            <Instagram title = "Blogs" instagrams = {props.blogs} link = "blog" />
        </div>
    );
}
 
const mapStatetoProps = state =>{   
    return {
        roomDetail: state.roomDetail,
        blogs: state.blogsRecent,
        roomsOrther: state.roomsOrther
    }
}
 

const mapDispatchtoProps = ( dispatch, ownProps) =>{    
    return {  
        fecthImagePanelRequest: () => {
            dispatch(actFecthImagePanelRequest())
        },
        fechBlogResentRequest: () =>{
            dispatch(actFechBlogResentRequest())
        },
        fechRoomDetailRequest: (slug) =>{
            dispatch(actfechRoomDetailRequest(slug));
        },
        fecthRoomOrtherRequest: (slug) =>{
            dispatch(actFecthRoomOrtherRequest(slug))
        }       
    }
}

export default connect(mapStatetoProps, mapDispatchtoProps) (RoomPageDetail);

