import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import Panel from '../Components/Panel';
import Rooms from '../Components/RoomPage/Rooms';
import Instagram from '../Components/Instagram';
import FormSearch from '../Components/RoomPage/FormSerch';
import FormEvaluate from '../Components/RoomPage/FormEvaluate';
import { actFecthImagePanelRequest } from '../redux/action/carousels';
import { actFechRoomsRequest, actfechCountRoomsRequest, actChangeCurrentPageRoom } from '../redux/action/rooms';
import { actFechBlogResentRequest } from '../redux/action/blogs';

function RoomPage(props) {   

    const history = props.history;
    const match = props.match;
    const [search, setSearch]          = useState({});    
    const [pagination, setPagination ] = useState({ totalItems: 0,
                                                    totalItemsperPage: 6,
                                                    currentPage: 1,
                                                    pageRanges: 3
                                                 })
   
   
    useEffect(() => {
        setSearch(props.search);       
    }, [props.search])
   
    let page = 1;
    if (match.params.page !== undefined && !isNaN(match.params.page)) {
        page = parseInt(match.params.page);        
    }    

    useEffect( ()=>{       
        props.changeCurrentPage(page)        
    },[page]) //eslint-disable-line

    useEffect(() => {
        props.fechCountRoomsRequest(search);       
     }, [search]) //eslint-disable-line

    useEffect(() => {
        setPagination(props.pagination);       
    }, [props.pagination])   
   
    //lấy dữ liệu
    useEffect(() => {       
        props.fecthImagePanelRequest();
        props.fechBlogRequest();
    }, []) //eslint-disable-line   

    useEffect(() => {              
        props.fecthRoomsRequest(search, pagination);
    }, [page, search]) //eslint-disable-line   
   
    return ( 
        <div >
            <Panel name='rooms' />
            <section className="ftco-section bg-light">
                <div className="container">
                    <div className="row">
                        <Rooms pagination={pagination} />
                        <div className="col-lg-3 sidebar">
                            <div className="sidebar-wrap bg-light ftco-animate">
                                <h3 className="heading mb-4">Advanced Search</h3>
                                <FormSearch search={props.search} history = { history } />
                            </div>
                            <div className="sidebar-wrap bg-light ftco-animate">
                                <h3 className="heading mb-4">Star Rating</h3>
                                <FormEvaluate history = { history } />
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            <Instagram title = "Blogs" instagrams = {props.blogs} link = "blog" />
        </div>
    );
}

const mapStatetoProps = state => {
    return {       
        search: state.roomsSearch,      
        pagination: state.paginationRoom,
        blogs: state.blogsRecent
    }
}

const mapDispatchtoProps = (dispatch, ownProps) => {
    return {
        fechCountRoomsRequest: (search) => {
            dispatch(actfechCountRoomsRequest(search))
        },
        fecthImagePanelRequest: () => {
            dispatch(actFecthImagePanelRequest())
        },
        fechBlogRequest: () =>{
            dispatch(actFechBlogResentRequest())
        },
        fecthRoomsRequest: (search, pagination) => {
            dispatch(actFechRoomsRequest(search, pagination));
        },
        changeCurrentPage: (page) =>{
            dispatch(actChangeCurrentPageRoom(page));
        }
    }
}
export default connect(mapStatetoProps, mapDispatchtoProps)(RoomPage);

