
import * as types   from '../constrants/actionType';

let initialState = [
    {
        name      : 'Welcome To Deluxe',
        content_1 :"Welcome To Deluxe",
        content_2 :"Hotels &amp Resorts",
        image     : 'bg_1.jpg'
    },
    {
        name      : 'Hello Sai Gon',
        content_1 :"Hello Sai Gon",
        content_2 :"Xin Chào Sài Gòn",
        image     : 'bg_2.jpg'
    }    
];
    
   
const carousel = (state = initialState, action) =>{

    switch(action.type){          
        case types.FECTH_CAROUSEL:   
            if(action.carousels.length >0){
                state = action.carousels;
            }           
            return [...state]; 
        default:
            return state;
    }
}

export default carousel;