
import * as types   from '../constrants/actionType';


let initialState = {
    totalItems: 0,
    totalItemsperPage: 8,
    currentPage: 1,
    pageRanges: 3,
};
    

   
const paginationBlog = (state = initialState, action) =>{   
   
    switch(action.type){  
        case types.SEARCH_BLOG:
            state.currentPage = 1;
            return state;   
        case types.FECTH_COUNT_BLOGS: 
            state.totalItems = action.countBlog;
            return state; 
        case types.CHANGE_CURRENT_PAGE_BLOG:           
            state.currentPage = action.page;
            return state;           
        default:
            return state;
    }
}

export default paginationBlog;