import React, { useState, useEffect} from 'react';

function ContactForm(props) {    
    const [ name, setName ]       = useState('');
    const [ email, setEmail ]     = useState('');
    const [ subject, setSubject ]  = useState('');
    const [ content, setContent ] = useState('');

    useEffect(()=>{
        setName(props.contact.name)
        setEmail(props.contact.email)
        setSubject(props.contact.subject)
        setContent(props.contact.content)
    },[props.contact])
   
    function handleSubmit(e){      
        e.preventDefault();
        let contact = { name, email, subject, content };
        const validate = checkContact(name, email, subject, content);
        if(validate.check){             
            props.onClickSubmit(contact); 
        }else{           
            props.onClickSubmitError(contact, validate.error)
        }      
    }

    const checkContact = (name, email, subject, content) =>{
        let check = true;
        let error = []
        if(name === ''){
            check = false;
            error.push({type: 'Tên', message: 'Phải Khác rỗng'});
        }
        if(subject === ''){
            check = false;
            error.push({type: 'Tiêu đề', message:  'Phải Khác rỗng'}); 
        }
        if(content === ''){
            check = false;
            error.push({type: 'nội dung', message:  'Phải Khác rỗng'})
        }
        if(validateEmail(email) === false){
            check = false;
            error.push({type: 'email', message:  'Phải là địa chỉ email!' })
        }
        return {check, error};
    }

    const validateEmail = (email)=> {
        var re = /^(([^<>()[\]\\.,;:\s@]+(\.[^<>()[\]\\.,;:\s@]+)*)|(.+))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return re.test(email);
    }       

    return (
        <form action="#" className="bg-white p-5 contact-form" onSubmit = {handleSubmit} >
            <div className="form-group">
                <input type="text" className="form-control" value={name} onChange={e => setName(e.target.value)} placeholder="Your Name" />
            </div>
            <div className="form-group">
                <input type="text" className="form-control" value={email} onChange={e => setEmail(e.target.value)} placeholder="Your Email" />
            </div>
            <div className="form-group">
                <input type="text" className="form-control" value={subject} onChange={e => setSubject(e.target.value)}  placeholder="Subject" />
            </div>
            <div className="form-group">
                <textarea name="true" cols={30} rows={7} value={content} onChange={e => setContent(e.target.value)} className="form-control" placeholder="Content"  />
            </div>
            <div className="form-group">
                <input type="submit" defaultValue="Send Message" className="btn btn-primary py-3 px-5" />
            </div>
        </form>
    );
} 

export default ContactForm;


