import {combineReducers }    from 'redux';

import rooms             from './rooms';
import roomsHome         from './rooms-home';
import roomsOrther       from './rooms-orther';
import roomDetail        from './roomDetail';
import roomsSearch       from './rooms-search';
import blogs             from './blogs';
import blogsHome         from './blogs-home';
import usersHome         from './users-home';
import blogDetail        from './blogDetail';
import blogsSearch       from './blogs-search';
import carousels         from './carousels';
import imagePanel        from './imagePanel';
import paginationRoom    from './paginationRoom';
import paginationBlog    from './paginationBlog';
import blogsRecent       from './blogs-recent';

const myReduce = combineReducers({    
    rooms,
    roomsHome,
    roomsOrther,
    roomDetail,   
    roomsSearch,
    blogs,
    blogsHome,    
    blogDetail,
    blogsRecent,
    blogsSearch,
    usersHome,
    carousels,
    imagePanel,    
    paginationRoom,
    paginationBlog
})

export default myReduce;
