import React, {useState, useEffect } from 'react';
import { connect } from 'react-redux';
import Panel from '../Components/Panel';
import Contact from '../Components/ContactPage/Contact';

import { actFechContactRequest } from '../redux/action/contact';


function ContactPage(props) {
  
    const [count, setCount] = useState(0);
    const [notify, setNotify]   = useState([]);
    const [contact, setContact ] = useState({ name: '', email: '',  subject: '', content: ''});

    useEffect( () =>{
        const list = document.querySelectorAll('script[src = "js/main.js"]');       
        if(list.length > 0){
            document.body.removeChild(list[0])
        }
        const script = document.createElement('script');
        script.src = 'js/main.js';
        document.body.appendChild(script);
    },[count])

    function handleSubmit(contact){ 
        props.onClickSubmit(contact);
        setNotify([{type: '', message:  'Cảm ơn bạn đã liên hệ'}]);
        setContact({ name: '', email: '',  subject: '', content: ''})
        loadAgainMainJs()
        props.history.push("/contact")
    }

    function handleSubmitError(contact, errors){
      
        setNotify(errors)
        setContact(contact)
       setCount(count + 1)
        loadAgainMainJs();
    }

    function loadAgainMainJs(){
        const list = document.querySelectorAll('script[src = "js/main.js"]');       
        if(list.length > 0){
            document.body.removeChild(list[0])
        }
        const script = document.createElement('script');
        script.src = 'js/main.js';
        document.body.appendChild(script);
    }

	return (
        <div >
            <Panel name = 'contact us' />             
            <Contact contact = {contact } notify ={notify} onClickSubmit = {handleSubmit} onClickSubmitError = {handleSubmitError} />
        </div>	
	);
}


const mapStatetoProps = state => {
    return {
        error: state.error       
    }
}


const mapDispatchtoProps = (dispatch, ownProps) => {
    return {      
        onClickSubmit: (contact) => {
            dispatch(actFechContactRequest(contact))
        }
    }
}

export default connect(mapStatetoProps, mapDispatchtoProps)(ContactPage);


