import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import Carousel from '../Components/HomePage/Carousel';
import Form from '../Components/HomePage/Form';
//import Introduce from '../Components/HomePage/Introduce';
import Procedure from '../Components/HomePage/Procedure';
import Rooms from '../Components/HomePage/Rooms';
import RoomItem from '../Components/HomePage/RoomItem';
import Couters from '../Components/HomePage/Couters';
import Employees from '../Components/HomePage/Employees';
import Blogs from '../Components/HomePage/Blogs';
import BlogItem from '../Components/HomePage/BlogItem';
import Instagram from '../Components/Instagram';
import { actFecthBlogsHomeRequest } from '../redux/action/blogs';
import { actFecthRoomsHomeRequest } from '../redux/action/rooms';

function HomePage(props) {
    const history =  props.history;     
    const [roomsHome, setRoomsHome] = useState([]);
    const [blogsHome, setBlogsHome] = useState([]);
   
    useEffect(() => {
        setRoomsHome(props.roomsHome);
    }, [props.roomsHome]);

    useEffect(() => {
        setBlogsHome(props.blogsHome);
    }, [props.blogsHome]);


    //khi việc kết nối dữ liệu giao cho các action thực hiện thực hiện
    useEffect(() => {
        props.fecthRoomsHomeRequest();
        props.fecthBlogsHomeRequest();
    }, []) //eslint-disable-line

    useEffect(() => {
        const list = document.querySelectorAll('script[src = "js/main.js"]');
        if (list.length > 0) {
            document.body.removeChild(list[0])
        }
        const script = document.createElement('script');
        script.src = 'js/main.js';
        document.body.appendChild(script);
    }, [props.roomsHome, props.blogsHome]);

    return (
        <div >
            <Carousel />
            <Form search={props.search} history = {history} />
            {/* <Introduce /> */}
            <Procedure />
            <Rooms > {showRoomList(roomsHome)}  </Rooms>
            <Couters />
            <Employees />
            <Blogs > {showBlogList(blogsHome)} </Blogs>
            <Instagram title="rooms" instagrams={roomsHome} link='article' />
        </div>
    );
}

const showRoomList = (rooms) => {
    let xhtml = '';
    if (rooms.length > 0) {
        xhtml = rooms.map((room, index) => {
            return <RoomItem key={index} room={room} />
        })
    }
    return xhtml;
}
const showBlogList = (blogs) => {
    let xhtml = '';
    if (blogs.length > 0) {
        xhtml = blogs.map((blog, index) => {
            return <BlogItem key={index} blog={blog} />
        })
    }
    return xhtml;
} 

const mapStatetoProps = state => {
    return {
        roomsHome: state.roomsHome,
        blogsHome: state.blogsHome,
        search: state.roomsSearch
    }
}

const mapDispatchtoProps = (dispatch, ownProps) => {
    return {
        fecthRoomsHomeRequest: () => {
            dispatch(actFecthRoomsHomeRequest())
        },
        fecthBlogsHomeRequest: () => {
            dispatch(actFecthBlogsHomeRequest())
        }
    }
}

export default connect(mapStatetoProps, mapDispatchtoProps)(HomePage);

