import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import Panel from '../Components/Panel';
import Blogs from '../Components/BlogPage/Blogs';
import { actfechBlogsRequest, actfechCountBlogsRequest, actChangeCurrentPageBlog, actChangeSearchBlog } from '../redux/action/blogs';


function BlogPage(props) {
     
    const match = props.match;   
    const [search, setSearch]          = useState({name: ''}); 
    const [blogs, setBlogs]          = useState([]);    
    const [pagination, setPagination ] = useState({ totalItems: 0,
                                                    totalItemsperPage: 8,
                                                    currentPage: 1,
                                                    pageRanges: 3
                                                 })
                                              
    useEffect(()=>{ 
        setSearch(props.search)       
    },[props.search])

       
    useEffect(()=>{
        setBlogs(props.blogs)
    },[props.blogs])

    let page = 1;
    if (match.params.page !== undefined && !isNaN(match.params.page)) {
        page = parseInt(match.params.page);        
    }    

    useEffect( ()=>{       
        props.changeCurrentPage(page)        
    },[page]) //eslint-disable-line

    useEffect(() => {
        props.fechCountBlogsRequest(search);       
     }, [search]) //eslint-disable-line

    useEffect(() => {
        setPagination(props.pagination);       
    }, [props.pagination])   
   
    
    useEffect(()=>{
        props.fechBlogsRequest(search, pagination);
    },[page, search]) //eslint-disable-line

    useEffect( () =>{
        const list = document.querySelectorAll('script[src = "js/main.js"]');       
        if(list.length > 0){
            document.body.removeChild(list[0])
        }
        const script = document.createElement('script');
        script.src = 'js/main.js';
        document.body.appendChild(script);
    },[])

	return (
        <div >
            <Panel name = 'blogs' />
            <Blogs  blogs = {blogs} pagination = {pagination} search = {search} name = {search.name}
                    onClickSubmit = {props.onClickSubmit} history = {props.history} />
        </div>	
	);
}


const mapStatetoProps = state => {
    return {              
        blogs: state.blogs,
        search: state.blogsSearch,
        pagination: state.paginationBlog
    }
}

const mapDispatchtoProps = (dispatch, ownProps) => {
    return {
        fechBlogsRequest: (search, pagination)=>{
            dispatch(actfechBlogsRequest(search, pagination));
        },
        fechCountBlogsRequest: (search) => {
            dispatch(actfechCountBlogsRequest(search))
        },
        changeCurrentPage: (page) =>{
            dispatch(actChangeCurrentPageBlog(page));
        },
        onClickSubmit: (search) => {
            dispatch(actChangeSearchBlog(search))
        } 
    }
}
export default connect(mapStatetoProps, mapDispatchtoProps)(BlogPage);

