
import * as types   from '../constrants/actionType';


const initialState = [
    {
        name      : 'Welcome To Deluxe',
        content_1 :"Welcome To Deluxe",
        content_2 :"Hotels &amp; Resorts",
        image     : 'bg_1.jpg'
    },
    
];
   
const imagePanel = (state = initialState, action) =>{  
   
    switch(action.type){          
        case types.FECTH_IMAGE_PANEL:    
            if(action.image.length >0){
                state = action.image;
            }
            return [...state];           

        default:
            return state;
    }
}

export default imagePanel;