import React       from 'react';
import ContactForm from './ContactForm';

function Contact(props) {
    return ( 
        <section className="ftco-section contact-section bg-light">
            <div className="container">
                <div className="row d-flex mb-5 contact-info">
                    <div className="col-md-12 mb-4">
                        <h2 className="h3" style = {{textAlign: 'center'}}>Contact Information</h2>
                    </div>                   
                </div>
                <div className="row">
                    <div className="col-lg-6"></div>
                    <div className="col-lg-6">                       
                        { showNotify(props.notify)}                       
                    </div>
                </div>
                <div className="row block-9">                   
                    <div className="col-md-6 order-md-last d-flex">
                        <ContactForm contact = {props.contact} onClickSubmit = {props.onClickSubmit} onClickSubmitError = {props.onClickSubmitError} />
                    </div>
                    <div className="col-md-6 d-flex">
                        <div id="map" className="bg-white" />
                    </div>
                </div>
            </div>
        </section>
    );
}

const showNotify = (notifys) =>{
    let xhtml = null;
    if(notifys.length === 0) return null;
    xhtml = notifys.map((item, index) =>{      
        let name = item.type.charAt(0).toUpperCase()+item.type.slice(1); 
        let msg  = item.message.charAt(0).toUpperCase()+item.message.slice(1);       
        if(name === '') return    <li key = {index}> {msg} </li>
        return ( 
            <li key = {index}><strong>{name}:</strong> {msg} </li>
        )
    })
    return  <div className="alert alert-danger close-btn" ><ul>{xhtml}</ul></div> ;
}
export default Contact;
