import * as types   from '../constrants/actionType';
import CallApi      from '../../Services/ApiCaller';


//carousels
export const actFecthCarouselHomeRequest = () =>{
    return async (dispacth) =>{ 
          return await CallApi('carousels/home', 'GET', null).then( (res) => {                        
              if(res !== undefined && res.status === 200 ){                 
                    dispacth(actFecthCarousels(res.data))                  
              }                    
          });
    };
}

export const actFecthCarousels = (carousels) =>{
    return { 
        type: types.FECTH_CAROUSEL,
        carousels
    }    
}


