const roomsOrther = (state = [], action) =>{
    
   
    switch(action.type){    
        
        case types.FECTH_ROOM_ORTHER:             
            state = action.roomOrther;
            return [...state];       

        default:
            return state;
    }
}

export default roomsOrther;