
import * as types   from '../constrants/actionType';


let initialState = {
    totalItems: 0,
    totalItemsperPage: 6,
    currentPage: 1,
    pageRanges: 3,
};
    

   
const paginationRoom = (state = initialState, action) =>{   
   
    switch(action.type){     
        case types.SEARCH_ROOM:
            state.currentPage = 1;
            return state;
        case types.FECTH_COUNT_ROOMS: 
            state.totalItems = action.countRooms;
            return state; 
        case types.CHANGE_CURRENT_PAGE_ROOM:
            state.currentPage = action.page;
            return state;    
        default:
            return state;
    }
}

export default paginationRoom;