import React, { useState, useEffect } from 'react';
import { connect }     from 'react-redux';
import PanelDetail     from '../Components/PanelDetail';
import BlogDetail      from '../Components/BlogPage/BlogDetail';
import { actFecthImagePanelRequest } from '../redux/action/carousels';
import { actFechBlogResentRequest, actfechBlogDetailRequest }  from '../redux/action/blogs';


function BlogPageDetail(props) {
    const slug   = props.match.params.slug;
    const history = props.history;
    const [blog, setBlog ] = useState({});  
    const [blogsRecent, setBlogsRecent ] = useState([]);


    useEffect(()=>{
        setBlog(props.blogDetail)
    },[props.blogDetail])

    useEffect(()=>{
        setBlogsRecent(props.blogsRecent)
    },[props.blogsRecent])

    useEffect(() => {       
        props.fecthImagePanelRequest();
        props.fechBlogResentRequest();             
    },[]) //eslint-disable-line

    useEffect(()=>{
        props.fechBlogDetailRequest(slug);
    },[slug]) //eslint-disable-line

    useEffect(() => {
        const list = document.querySelectorAll('script[src = "js/main.js"]');
        if (list.length > 0) {
            document.body.removeChild(list[0])
        }
        const script = document.createElement('script');
        script.src = 'js/main.js';
        document.body.appendChild(script);
    }, [])

    return (
        <div >
            <PanelDetail name = "blog single" link = "/blogs/1" history = {history} />            
            <BlogDetail blog ={blog} blogsRecent = { blogsRecent} history = {history} />           
        </div>
    );
}

const mapStatetoProps = state =>{   
    return {
        blogDetail: state.blogDetail,
        blogsRecent: state.blogsRecent
    }
}


const mapDispatchtoProps = ( dispatch, ownProps) =>{    
    return {  
        fecthImagePanelRequest: () => {
            dispatch(actFecthImagePanelRequest())
        },
        fechBlogResentRequest: () =>{
            dispatch(actFechBlogResentRequest())
        },
        fechBlogDetailRequest: (slug) =>{
            dispatch(actfechBlogDetailRequest(slug));
        },
        // fecthRoomOrtherRequest: (slug) =>{
        //     dispatch(actFecthRoomOrtherRequest(slug))
        // }  
    }
}

export default connect(mapStatetoProps, mapDispatchtoProps) (BlogPageDetail);

