import * as types from '../constrants/actionType';
import CallApi from '../../Services/ApiCaller';


//lấy rooms-home
export const actFecthRoomsHomeRequest = () => {
    return async (dispacth) => {
        return await CallApi('rooms/roomhome', 'GET', null).then((res) => {
            if (res !== undefined && (res.status === 200 || res.status === 304)) {
                dispacth(actFecthRoomsHome(res.data))
            }

        }); 
    }; 
}

export const actFecthRoomsHome = (rooms) => {
    return {
        type: types.FECTH_ROOMS_HOME,
        rooms
    }
}

//lấy rooms cho roomspage
export const actFechRoomsRequest = (search, pagination) => {      
    return async (dispacth) => {
        return await CallApi('rooms/roompage', 'GET', {search, pagination}).then((res) => { //search, pagination là 1 đối tượng
            if (res !== undefined && (res.status === 200 || res.status === 304)) {
                dispacth(actFecthRooms(res.data))
            }
        });
    };   
}

export const actFecthRooms = (rooms) => {
    return {
        type: types.FECTH_ROOMS,
        rooms
    }
}

