import React, { useState, useEffect }  from 'react';
import { connect }                     from 'react-redux';
import { actFecthCarouselHomeRequest } from '../../redux/action/carousels';

function Carourel(props) {
    const [carousels, setCarousels] = useState([]);
    const [isShow, setisShow]       = useState(false);
    const forderImage               =  'uploads/imageweb';
    

    useEffect(() => {
        props.fecthCarouselHomeRequest();
    },[]) //eslint-disable-line

    useEffect(() => {
        setCarousels(props.carousels);
    },[props.carousels])

    useEffect(() => {
        if (carousels.length > 0)
            setisShow(true);
    },[carousels.length])

    useEffect(() => {
        const list = document.querySelectorAll('script[src = "js/main.js"]');
        if (list.length > 0) {
            document.body.removeChild(list[0])
        }
        const script = document.createElement('script');
        script.src = 'js/main.js';
        document.body.appendChild(script);
    },[props.carousels]);

    
    const showCarousel = (carousels) => {
        let xhtml = '';
        xhtml = carousels.map((carousel, index) => {
            return (
                <div className="slider-item" style={{ backgroundImage: `url( ${forderImage}/${carousel.image})` }} key = {index}>
                    <div className="overlay" />
                    <div className="container">
                        <div className="row no-gutters slider-text align-items-center justify-content-center">
                            <div className="col-md-12 ftco-animate text-center">
                                <div className="text mb-5 pb-3">
                                    <h1 className="mb-3">{carousel.content_1}</h1>
                                    <h2>{carousel.content_2}</h2>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            )
        })
        return <section className="home-slider owl-carousel"> {xhtml} </section>;
    }

    return <div>  {isShow ? showCarousel(carousels) : ''}</div>
}

const mapStatetoProps = state => {
    return {
        carousels: state.carousels
    }
}

const mapDispatchtoProps = (dispatch) => {
    return {
        fecthCarouselHomeRequest: () => {
            dispatch(actFecthCarouselHomeRequest())
        }
    }
}

export default connect(mapStatetoProps, mapDispatchtoProps)(Carourel);

